import
{ createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import {  SystemStatus } from './interface';
import { STATUS } from '../constant';
import { CoinKey } from '@loopring-web/component-lib/src/static-resource';

const initialState:Required<SystemStatus>  = {
    system: {},
    status:'UNSET',
    errorMessage:null,
}
const systemSlice:Slice = createSlice({
    name: 'system',
    initialState,
    reducers: {
        updateSystem(state, action:PayloadAction<Partial<SystemStatus>>) {
            state.status = STATUS.PENDING
        },
        getSystemStatus(state, action: PayloadAction<Partial<SystemStatus>>) {
            // @ts-ignore
            if (action.error) {
                state.status =  STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            state.system = {...state.system,...action.payload.system};
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }

    },
});
export  { systemSlice };
export const { updateSystem, getSystemStatus, statusUnset } = systemSlice.actions;