import React from 'react';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import {
    AmmDetail,
    CustomError,
    ErrorMap,

} from '@loopring-web/component-lib/src/static-resource';
import {  useTicker } from '../../../stores/ticker';
import { PoolsRow } from '@loopring-web/component-lib';
import { deepClone } from '../../../utils/obj_tools';
import { STATUS } from '../../../stores/constant';

export function useAmmMapUI<R extends Object,I extends Object>({pageSize}:{pageSize:number}) {
    const [rawData,setRawData] = React.useState<Array<AmmDetail<any>>|[]>([]);
    const [page,setPage] = React.useState<number>(1);
    // const [_ammTradeMap,setAmmTradeMap]  = React.useState<{[key in keyof R]?:PoolsRow<I>}>({});
    const {
        ammMap,
        status: ammMapStatus,
        errorMessage: errorAmmMap,
        statusUnset:ammMapStatusUnset,
        updateAmmMap,
    } = useAmmMap();
    const {
        tickerMap,
        status: tickerStatus,
        errorMessage: errorTickerMap,
        statusUnset: tickerStatusUnset ,
        updateTickers,
    } = useTicker();
    const updateRawData = ()=>{
        try {
            const _ammMap:PoolsRow<I>  = deepClone(ammMap);
            for (let tickerMapKey in tickerMap) {
                _ammMap[tickerMapKey].tradeFloat =  tickerMap[tickerMapKey]
            }
            let data = (ammMap && ammMap !== {}) ? Object.keys(ammMap).map((ammKey:string) => {
                // @ts-ignore
                return _ammMap[ammKey]
            }):[]
            setRawData(data)
        }catch (error){
            throw new CustomError({...ErrorMap.NO_TOKEN_MAP,options:error})
        }

    }



    const updateTickersUI = React.useCallback((_page)=>  {
        // if(page !== _page)  {
            setPage(_page);
            if(ammMap && Object.keys(ammMap).length>0){
                // console.log('page get ticker',Object.keys(ammMap).length,page - 1 , page - 1 * pageSize + pageSize);
                const keys = []
                for (let i =  ( page - 1) * pageSize; i<Object.keys(ammMap).length && i <  (page - 1) * pageSize + pageSize   ;i++ ){
                    keys.push(Object.keys(ammMap)[i]);
                }
                updateTickers(keys);
            }
        // }
        // else{
        //
        // }
    },[ammMap,page,pageSize,updateTickers])
    React.useEffect(() => {
        switch (tickerStatus) {
            case "ERROR":
                console.log("ERROR",'get ticker error,ui');
                tickerStatusUnset()
                break;
            case "PENDING":
                break;
            case "DONE":
                tickerStatusUnset()
                updateRawData()
                break;
            default:
                break;
        }
    }, [tickerStatus, tickerStatusUnset]);

    // init AmmMap at begin
    React.useEffect(() => {
        if (!ammMap || Object.keys(ammMap).length === 0) {
            updateAmmMap();
        } else{
            updateTickersUI(page)
        }
    },[]);



    React.useEffect(() => {
        switch (ammMapStatus) {
            case "ERROR":
                console.log("ERROR",'get ammMap error,ui');
                ammMapStatusUnset();
                break;
            case STATUS.PENDING:
                console.log("PENDING",'get ammMap error,ui');
                // ammMapStatusUnset();
                break;
            case STATUS.DONE:
                ammMapStatusUnset();
                updateTickersUI(1)
                break;
            default :
                break;
        }
    }, [ammMapStatus,ammMapStatusUnset,updateTickersUI]);
    return {
        page,
        rawData,
        updateTickersUI,
    }
}

