import { useState } from 'react'

import { useAmmpoolAPI, useUserAPI } from 'hooks/exchange/useApi'
import { useAccount } from 'stores/account/hook'

import { AmmTrade, FilledType, dumpError400 } from 'loopring-sdk'

import { useCustomDCEffect } from 'hooks/common/useCustomDCEffect'

import { AccountStatus } from 'state_machine/account_machine_spec'

import { RawDataTradeItem } from '@loopring-web/component-lib'

export function useGetMyTrades(market: any) {

  const { apiKey, accountId, account, } = useAccount()

  const userApi = useUserAPI()

  const [myTrades, setMyTrades] = useState<any>([])

  useCustomDCEffect(async () => {
    if (!userApi || !accountId || !apiKey || !market) {
      return
    }

    let tradesNew: any = []

    if (account.status === AccountStatus.ACTIVATED) {

      try {

        const trades = await userApi.getUserTrades({
          accountId,
          market: market.amm,
          fillTypes: FilledType.amm,
        }, apiKey)

        trades.trades.forEach((item: any) => {
          const itemTmp: RawDataTradeItem = {

            side: item[2],
            amount: {
              from: {
                key: market?.baseShow,
                value: item[2]
              },
              to: {
                key: market?.quoteShow,
                value: item[3]
              }
            },
            price: {
              key: market?.baseShow,
              value: item[4]
            },
            fee: {
              key: market?.baseShow,
              value: item[6]
            },
            time: item[0]
          }
          tradesNew.push(itemTmp)
          // tradesNew.push([item[2], item[3], item[4], item[6], item[0]])
        })

        console.log('market:', market, ' trades:', trades.trades, ' tradesNew:', tradesNew)

      } catch (reason) {
        dumpError400(reason)
      }

    }

    setMyTrades(tradesNew)

  }, [userApi, accountId, apiKey, market, account.status])

  return {
    myTrades,
  }
}

export function useGetRecentTrades(ammPoolAddress: string) {

  const ammPoolApi = useAmmpoolAPI()

  const [recentTrades, setRecentTrades] = useState<any>([])

  useCustomDCEffect(() => {
    if (!ammPoolApi || !ammPoolAddress) {
      return
    }

    ammPoolApi.getAmmPoolTrades({
      ammPoolAddress
    }).then((trades) => {
      let tradesNew: any = []
      trades.trades.forEach((item: AmmTrade) => {
        // tradesNew.push([item.side, item.price, item.feeAmount, item.createdAt])
      })

      console.log('useGetRecentTrades trades:', trades.trades, ' tradesNew:', tradesNew)
      setRecentTrades(tradesNew)
    }).catch((reason) => {
      dumpError400(reason)
    })

  }, [ammPoolApi, ammPoolAddress])

  return {
    recentTrades,
  }
}
