import { all, takeLatest, call, put } from "redux-saga/effects";
import { getAmmMap, getAmmMapStatus } from './reducer';
import { AmmDetail, CoinInfo, PairKey } from '@loopring-web/component-lib/src/static-resource';
import { ErrorMap } from '@loopring-web/component-lib/src/static-resource/';
import store from '../../index';
import { AmmpoolAPI, getTokenInfoById, AmmPoolInfoV3, AmmPoolStat, } from "loopring-sdk";
import { asyncGetTradingInfo, getTradingInfo } from "stores/trading/hook";

import { makeCoinInfo } from 'utils/swap_calc_utils'
import { exchangeAPI, userAPI } from "stores/apis/api";

type AmmMap<R extends object> = Map<PairKey<R>,AmmDetail<R>> | {}
const getAmmMapApi = async <R extends object>() => {

    let data: AmmMap<R> = {}
    
    const {
        tokens,
        ammpools,
        ammPoolStats,
    } = await asyncGetTradingInfo()

    const faitPrice = await exchangeAPI().getFiatPrice({ legal: 'CNY' })

    const forex = faitPrice.prices['USDT']

    const keys = Reflect.ownKeys(ammpools)

    keys.forEach(async(key: any) => {
        const item: AmmPoolInfoV3 = ammpools[key]
        const tokenA = getTokenInfoById(tokens.tokenIdMap, parseInt(item.tokens.pooled[0]))
        const tokenB = getTokenInfoById(tokens.tokenIdMap, parseInt(item.tokens.pooled[1]))

        const ammPoolStat: AmmPoolStat = ammPoolStats[item.market]

        const usdLiquidity = parseFloat(ammPoolStat.liquidityUSD)

        // const balance = await userAPI().getUserBalances({accountId: 0, tokens: ''}, '')

        const dataItem: AmmDetail<R> = {
            coinAInfo: makeCoinInfo(tokenA) as CoinInfo<any>,
            coinBInfo: makeCoinInfo(tokenB) as CoinInfo<any>,
            amountDollar: usdLiquidity,
            amountYuan: usdLiquidity / forex,
            totalLPToken: parseFloat(ammPoolStat.lpLiquidity),
            totalA: parseInt(ammPoolStat.liquidity[0]),
            totalB: parseInt(ammPoolStat.liquidity[1]),
            rewardValue: ammPoolStat.rewards[1],
            rewardToken: ammPoolStat.rewards[0],
            feeA:parseInt(ammPoolStat.fees[0]),
            feeB:parseInt(ammPoolStat.fees[1]),
            feeDollar: 100,  //TODO coinA price * feeA + price * feeB
            feeYuan: 100, //TODO coinA price * feeA + price * feeB  /forex
            isNew: true,
            isActivity: false,
        }
        if (item.market) {
            //@ts-ignore
            data[item.market] = dataItem
        }
    })

    return { data }
}

export function* getPostsSaga() {
    try {
        const { data } = yield call(getAmmMapApi);
        console.log('--- getPostsSaga:', data)
        yield put(getAmmMapStatus({ammMap:data}));
    } catch (err) {
        yield put(getAmmMapStatus(err));
    }
}

export default function* ammMapSaga() {
    yield all([takeLatest(getAmmMap, getPostsSaga)]);
}
