import { AmmpoolAPI, ExchangeAPI, UserAPI, WsAPI } from "loopring-sdk"
import { getTradingInfo } from 'stores/trading/hook'

export const userAPI = () => {
    return new UserAPI(getTradingInfo().chainId)
}

export const exchangeAPI = () => {
    return new ExchangeAPI(getTradingInfo().chainId)
}

export const ammpoolAPI = () => {
    return new AmmpoolAPI(getTradingInfo().chainId)
}

export const wsAPI = () => {
    return new WsAPI(getTradingInfo().chainId)
}
