import { useTokenMap } from './stores/token';
import { useAmmMap } from './stores/Amm/AmmMap';
import React, { useState } from 'react';
import { ENV, NETWORK, SystemStatus, useSystem } from './stores/system';
import { ChainId } from 'loopring-sdk';
import detectEthereumProvider from '@metamask/detect-provider';
import { useCustomDCEffect } from 'hooks/common/useCustomDCEffect';

/**
 * @description call it when bootstrap the page or change the network
 */
export function useInit(){
    const systemState = useSystem();
    const tokenState = useTokenMap();
    const ammMapState = useAmmMap();

    // const wallectState = useWalletStatus();
    //init function do only once
    let web3:any;

    //TODO register when network changed update
    //systemState.updateSystem({network:NETWORK.MAIN})

  

    useCustomDCEffect(async() => {

        const handleChainChanged = (chainId: number) => {
    
            const network = chainId == ChainId.GORLI ? NETWORK.Goerli : NETWORK.MAIN       
            systemState.updateSystem({ network })

        }

        const provider: any = await detectEthereumProvider()
        if (provider) {
            const chainId = await provider.request({ method: 'eth_chainId' });
    
            provider.on('chainChanged', handleChainChanged)

            handleChainChanged(chainId)
            
        } else {
            systemState.updateSystem({network: NETWORK.MAIN})
            console.log('web3 is not found')
        }
     

        //TODO if have  metaMask plugin
        // tokenState.updateTokenMap();
        // ammMapState.updateTokenMap();
    }, [])

    React.useEffect(() => {
        switch (systemState.status) {
            case "ERROR":
                systemState.statusUnset();
                //TODO show error at button page show error  some retry dispat again
                break;
            case "DONE":
                systemState.statusUnset();
                //TODO do some static information update
                tokenState.updateTokenMap();
                ammMapState.updateAmmMap();
                break;
            default:
                break;

        }
    },[systemState.status,systemState.statusUnset,tokenState.updateTokenMap,ammMapState.updateAmmMap]);

    const ammMapCheck = React.useCallback(()=>{
        if(ammMapState.status ===  "ERROR"){
            //TODO show error at button page show error  some retry dispat again

        }
        return ammMapState.status
    },[ammMapState.status])

    React.useEffect(() => {


        switch (tokenState.status) {
            case "ERROR":
                //TODO show error at button page show error  some retry dispat again

            case "DONE":
                if(ammMapCheck() !== "PENDING") {
                    tokenState.statusUnset();
                    ammMapState.statusUnset();
                }
                break;
            default:
                break;

        }
    },[tokenState.status,ammMapState.status,tokenState.updateTokenMap]);

    return {
        status: systemState.status,
    }

}
