import { StateBase } from '../interface';

export enum ENV {
    DEV='DEV',
    UAT='UAT',
    PROD='PROD',
}
export enum NETWORK {
    Goerli='Goerli',
    MAIN='MAIN',
    NONETWORK='NONETWORK'
}

export type System = {
    env:keyof typeof ENV,
    network:keyof typeof NETWORK,
    baseURL: string,
}

export type SystemStatus = {
    system:System | {}
}  & StateBase



