import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import {  TokenMapStates } from './interface';
import { STATUS } from '../constant';

const initialState:Required<TokenMapStates>  = {
    tokenMap:{},
    status:'UNSET',
    errorMessage:null,
}
const tokenMapSlice:Slice = createSlice({
    name: 'tokenMap',
    initialState,
    reducers: {
        getTokenMap(state, action:PayloadAction<string | undefined>) {
            state.status = STATUS.PENDING
        },
        getTokenMapStatus(state, action: PayloadAction<TokenMapStates>) {
            // @ts-ignore
            if (action.error) {
                state.status =  STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            state.tokenMap = {...action.payload.tokenMap};
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }

    },
});
export { tokenMapSlice };
export const { getTokenMap, getTokenMapStatus, statusUnset } = tokenMapSlice.actions;