export function useCoinMap(base: string) {

  const { ammpools } = useGetAmmPools()

  const { tokens } = useGetTokens()

  const [left, setLeft] = useState<CoinMap<any, CoinInfo<any>>>({})

  const [right, setRight] = useState<CoinMap<any, CoinInfo<any>>>({})

  useCustomDCEffect(() => {
  
    if (!ammpools || !tokens) {
        return
    }
  
    const left = getLeft(ammpools.pairs, tokens)
  
    if (left) {
        setLeft(left.coinMapLeft)
  
        const right = getRight(ammpools.pairs, base, tokens)
  
        if (right) {
            setRight(right.coinMapRight)
        }
  
    }
  
  }, [ammpools, tokens, base])

  return {
      left,
      right,
      setLeft,
      setRight,
  }

}

export function useSwapNew() {

  // get trading pair from url path.
  const match: any = useRouteMatch(":symbol")

  //const symbol =  ad?.hasOwnProperty()?'':undefined;
   const symbol =  match?.params.symbol ?? undefined;

  // user info
  const { apiKey, eddsaKey, accountId, account: globalAcc, } = useAccount()

  // total info of exchange/tokens/markets/ammpools
  const { markets } = useGetMixMarkets()

  const { tokens } = useGetTokens()

  const { exchangeInfo } = useGetExchangeInfo()

  const { ammpools } = useGetAmmPools()

  const { ammPoolsBalances } = useGetAmmPoolsBalances()

  // state for shown elements
  const [base, setBase] = useState<any>('')

  const [quote, setQuote] = useState<any>('')

  const {
      left,
      right,
      setRight,
  } = useCoinMap(base)

  const [market, setMarket] = useState<any>()

  const [poolAddress, setPoolAddress] = useState<string>('')

  const { balances, setBalances } = useGetUserBalances(tokens?.tokenSymbolMap)

  const [balance, setBalance] = useState<any>(0)
  const [availToShow, setAvailToShow] = useState<any>(0)

  const [fee, setFee] = useState(0)
  const [takerRate, setTakerRate] = useState(10)

  const [currentPrice, setCurrentPrice] = useState<number>(0)

  const [ammOrder, setAmmOrder] = useState<string>(UserStorage.getAmmOrder())

  const [slippageTolerance, setSlippageTolerance] = useState(0.01)

  const [priceImpact, setPriceImpact] = useState(0)

  const [baseAmt, setBaseAmt] = useState<string | number>()

  const [quoteAmt, setQuoteAmt] = useState<number>(0)

  const [amount1ApplyOrderFee, setAmount1ApplyOrderFee] = useState(0)

  const minimumReceived = quoteAmt ? quoteAmt * (1 - slippageTolerance) : 0

  // const quoteUserBalance = useGetUserBalances(tokens, quote)
  const [quoteBalance, setQuoteBalance] = useState<any>(0)
  const [quoteToShow, setQuoteToShow] = useState<any>(0)

  const [swapProps, setSwapProps] = useState<any>({})

  // apis
  const userApi = useUserAPI()

  const [walletMap, setWalletMap] = useState<WalletMap<any, WalletCoin<any>>>({})
  
  //websocket update

  const {
    account,
    ammPool,
    order,
  } = useSwapPageWs(poolAddress, market, apiKey)

  // set base and quote according to the url matcher.
  // e.g. /trading/lite/LRC-USDT base => LRC quote => USDT
  useCustomDCEffect(() => {
    console.log('----------------!!!!!!!!!!!', symbol)

    if (!symbol) {

    console.log('----------------getBaseQuote LRC_ETH')
      setBase('LRC')
      setQuote('ETH')
    } else {
      const { base, quote } = getBaseQuote(symbol)

      if (base && quote) {

        console.log('getBaseQuote base:', base, ' quote:', quote)

        setBase(base)
        setQuote(quote)
      }
    }

  }, [symbol, markets, setBase, setQuote, setMarket])

  // setMarket when base or quote changed.
  useCustomDCEffect(() => {

    if (!markets) {
      return
    }

    const { marketArr } = markets

    setMarket(getExistedMarket(marketArr, base, quote))

  }, [markets, setMarket, base, quote])

  // update pooladdress
  useCustomDCEffect(() => {
  
    if (!base || !quote || !ammpools || !ammPoolsBalances) {
      return
    }
  
    const { ammInfo, } = getAmmInfo(ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, base, quote)
  
    console.log('ammInfo.market:', ammInfo.market)
  
    // setMarket(ammInfo.market)
    setPoolAddress(ammInfo.address)
  
  }, [base, quote, ammpools, ammPoolsBalances,])

  useCustomDCEffect(() => {

      if (!balances || !base || !tokens) {
          return
      }

      if (globalAcc.status === AccountStatus.ACTIVATED) {

          const balance = balances.symbol[base]
          setBalance(balance)
          setAvailToShow(fromWEI(tokens.tokenSymbolMap, base, balance))

          // const quoteBalance = balances.symbol[quote]
          // setQuoteBalance(quoteBalance)
          // setQuoteToShow(fromWEI(tokens, quote, quoteBalance))

      } else {

          setBalance(0)
          setAvailToShow(0)

          // setQuoteBalance(0)
          // setQuoteToShow(0)

      }

  }, [balances, base, tokens, globalAcc.status])


  useCustomDCEffect(() => {
    if (!account || !tokens || !base) {
        return
    }

    if (globalAcc.status === AccountStatus.ACTIVATED) {

        const currentBalance = balances.tokenId[account.tokenId]
        const totalAmount = fm.toBig(account.totalAmount)
        const amountLocked = fm.toBig(account.amountLocked)
        const newAvailable = totalAmount.minus(amountLocked)

        if (!newAvailable.eq(fm.toBig(currentBalance))) {
            const avail = fromWEI(tokens.tokenSymbolMap, base, newAvailable.toString())

            const balancesTmp = deepClone(balances)
            balancesTmp.tokenId[account.tokenId] = newAvailable.toString()
            const token = balancesTmp.map[account.tokenId]
            balancesTmp.symbol[token.symbol] = newAvailable.toString()
            setBalances(balancesTmp)
        }

    } else {
        setBalances(undefined)
    }

}, [account, tokens, base, globalAcc.status])

useCustomDCEffect(() => {

    if (!tokens) {
        return
    }

    let walletMap: WalletMap<any, WalletCoin<any>> = {}

    if (globalAcc.status === AccountStatus.ACTIVATED) {
        if (balances && balances.symbol) {
            const keys = Reflect.ownKeys(balances.symbol)

            keys.forEach((item: any) => {
                const balance = balances.symbol[item]
                walletMap[item] = {
                    belong: item,
                    count: fromWEI(tokens.tokenSymbolMap, item, balance) as number,
                }
            })
        }

    }

    setWalletMap(walletMap)

}, [balances, tokens, globalAcc.status])

useCustomDCEffect(() => {

  if (!base || !quote || !ammpools || !ammPoolsBalances || !tokens) {
      console.log('return!!! --->setQuoteBalance')
      return
  }

  const {
      ammInfo,
      baseBalance,
      quoteBalance,
  } = getBalances(ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, base, quote, tokens.tokenSymbolMap)

  setFee(ammInfo.feeBips)
  // setQuoteBalance(quoteBalance.volume)
  // setQuoteToShow(quoteShow)

  if (baseBalance.volume > 0) {
      setCurrentPrice(quoteBalance.volume / baseBalance.volume)
  } else {
      setCurrentPrice(0)
  }

}, [tokens, base, quote, ammpools, ammPoolsBalances,])

useCustomDCEffect(async () => {

  if (!apiKey && !accountId && !userApi && !ammOrder) {
      return
  }

  const fn = (accountId: number, orderHash: string, apiKey: string, count = 0) => {
      // console.log('orderHash:', orderHash, ' count:', count)
      return new Promise((resolve, reject) => {
          if (orderHash) {
              userApi.getOrderDetails({
                  accountId,
                  orderHash
              }, apiKey).then((order) => {
                  console.log('order.status:', order.status)
                  if (order.status === TxStatus.processed) {
                      UserStorage.clearAmmOrder()
                      return resolve(OrderHandling.processed)
                  } else {
                      if (count < 10) {
                          setTimeout(() => {
                              fn(accountId, UserStorage.getAmmOrder(), apiKey, count++)
                          }, 1000)
                      } else {
                          reject(OrderHandling.too_many_times)
                      }
                  }
              }).catch(() => {
                  UserStorage.clearAmmOrder()
                  return reject(OrderHandling.no_such_order)
              })
          }
      })
  }

  fn(accountId, UserStorage.getAmmOrder(), apiKey).then((result) => {
      console.log('then:', result)
  }).catch((reason) => {
      dumpError400(reason)
  })

  return () => {
  }

}, [apiKey, accountId, ammOrder, userApi])

useCustomDCEffect(() => {

  if (!userApi || !accountId || !apiKey || !base || !quote || !ammpools || !ammPoolsBalances || !tokens) {
      return
  }

  let mounted = true

  const { ammInfo, } = getBalances(ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, base, quote, tokens.tokenSymbolMap)

  userApi.getUserFeeRate({
      accountId,
      markets: ammInfo.market
  }, apiKey).then((response) => {
      if (mounted) {
          setTakerRate(response[0].takerRate)
      } else {
          console.log('setTakerRate unmounted!')
      }
  }).catch((reason) => {
      dumpError400(reason, 'after getUserFeeRate')
  })

  return () => {
      mounted = false
  }

}, [tokens, userApi, accountId, apiKey, base, quote, ammpools, ammPoolsBalances, globalAcc.status])

const onBaseAmtInputChange = useCallback((event: any) => {

  if (!ammpools || !ammPoolsBalances || !tokens) {
      return
  }

  let baseAmtVal = event.tradeValue as number

  // console.log('onBaseAmtInputChange baseAmtVal:', baseAmtVal, ' baseAmt:', baseAmt)
  if (baseAmtVal !== undefined && baseAmtVal === baseAmt) {
      console.log('return directly')
      return
  } else if (baseAmtVal === undefined) {
      baseAmtVal = 0
  }

  const {
      quoteAmt: quoteAmtVal,
      _amount1ApplyOrderFee,
      priceImpact,
  } = getAmountOut(baseAmtVal, ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, 
    tokens.tokenSymbolMap, base, quote, takerRate, currentPrice)

  const { isNormal, } = checkStatus(baseAmtVal, balance, quoteAmtVal, fromWEI(tokens.tokenSymbolMap, quote, quoteBalance))

  if (isNormal) {
      setBaseAmt(baseAmtVal)
      setQuoteAmt(quoteAmtVal)
      setAmount1ApplyOrderFee(_amount1ApplyOrderFee)
      setPriceImpact(priceImpact)
  } else {
      setQuoteAmt(0)
      setAmount1ApplyOrderFee(0)
      setPriceImpact(0)
  }

  return quoteAmtVal

}, [tokens, baseAmt, balance, quoteBalance, ammpools, ammPoolsBalances, base, quote, takerRate, currentPrice])

const onQuoteAmtInputChange = useCallback((event: any) => {

  if (!ammpools || !ammPoolsBalances || !tokens) {
      return
  }

  let quoteAmtVal = event.tradeValue as number

  // console.log('onQuoteAmtInputChange quoteAmtVal:', quoteAmtVal, ' quoteAmt:', quoteAmt)
  if (quoteAmtVal !== undefined && quoteAmtVal === quoteAmt) {
      // console.log('return directly')
      return
  } else if (quoteAmtVal === undefined) {
      quoteAmtVal = 0
  }

  const {
      baseAmt: baseAmtVal,
      _amount1ApplyOrderFee,
      priceImpact,
      hasError,
  } = getAmountOut_Reverse(quoteAmtVal, ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, 
    tokens.tokenSymbolMap, base, quote, takerRate, currentPrice)

  const { isNormal, } = checkStatus(baseAmtVal, balance, quoteAmtVal, fromWEI(tokens.tokenSymbolMap, quote, quoteBalance))

  if (!hasError && isNormal) {
      setBaseAmt(baseAmtVal)
      setQuoteAmt(quoteAmtVal)
      setAmount1ApplyOrderFee(_amount1ApplyOrderFee)
      setPriceImpact(priceImpact)
  } else {
      setBaseAmt(0)
      setAmount1ApplyOrderFee(0)
      setPriceImpact(0)
  }

  return baseAmtVal

}, [tokens, quoteAmt, balance, quoteBalance, ammpools, ammPoolsBalances, base, quote, takerRate, currentPrice])

const onSwap = useCallback(() => {
  console.log('try to swap ', baseAmt, base, ' -> ', quoteAmt, quote)

  const swap = async () => {

      if (!exchangeInfo?.exchangeAddress) {
          throw Error('no exchangeAddress!')
      }

      if (!baseAmt || !quoteAmt || !ammpools || !tokens) {
          // throw Error('no baseAmt or quoteAmt!')
          console.log('no baseAmt or quoteAmt!')
          return
      }

      const {
          ammInfo,
          feeBips,
      } = getAmmInfo(ammpools.ammpools, ammPoolsBalances.ammpoolsbalances, base, quote)

      const allOrNone = false

      const baseToken = getToken(tokens.tokenSymbolMap, base)

      const quoteToken = getToken(tokens.tokenSymbolMap, quote)

      // const storageId = await authKeyApi.getNextStorageId(accountId, baseToken.tokenId, apiKey)

      let orderId: number
      try {

          const storageId = await userApi.getNextStorageId({
              accountId,
              sellTokenId: baseToken.tokenId
          }, apiKey
          )
          // console.log('storageId:', storageId)

          orderId = storageId.orderId

      } catch (reason) {
          dumpError400(reason, 'getNextStorageId')
          throw Error('getNextStorageId failed')
      }

      // const validUntil = Math.ceil(new Date().getTime() / 1000) + 3600 * 1000
      const validUntil = 1700000000

      const amount0InBigNumber = toWEI(tokens.tokenSymbolMap, base, baseAmt)
      const amount1WithSlippage = applySlippageTolerance(
          tokens.tokenSymbolMap,
          quote,
          amount1ApplyOrderFee,
          slippageTolerance,
      )

      const amount1InBigNumber = new BigNumber(amount1WithSlippage.outInWei).toFixed()

      try {

          console.log('amount0InBigNumber:', amount0InBigNumber, ' amount1InBigNumber:', amount1InBigNumber)

          const request: SubmitOrderRequestV3 = {
              exchange: exchangeInfo?.exchangeAddress,
              accountId,
              storageId: orderId,
              sellToken: {
                  tokenId: baseToken.tokenId,
                  volume: amount0InBigNumber.toString()
              },
              buyToken: {
                  tokenId: quoteToken.tokenId,
                  volume: amount1InBigNumber.toString()
              },
              allOrNone,
              validUntil,
              maxFeeBips: 60,
              fillAmountBOrS: false,
              orderType: OrderType.ClassAmm,
              eddsaSignature: '',
          }

          const response = await userApi.submitOrder(request, eddsaKey, apiKey)

          console.log('response:', response)

          UserStorage.setAmmOrder(response.hash)
          setAmmOrder(response.hash)

      } catch (reason) {
          dumpError400(reason, 'submitOrderV3')
      }

  }

  swap()

}, [accountId, ammPoolsBalances, ammpools, amount1ApplyOrderFee, apiKey,
  base, baseAmt, eddsaKey, exchangeInfo?.exchangeAddress,
  quote, quoteAmt, slippageTolerance, tokens, userApi])

useCustomDCEffect(() => {

  const tradeCalcData: TradeCalcData<any> = {
      coinSell: base, //name
      coinBuy: quote,
      StoB: parseFloat(currentPrice?.toFixed(4)),
      BtoS: 0,
      sellCoinInfoMap: left,
      buyCoinInfoMap: right,
      walletMap: walletMap,
      slippage: slippageTolerance * 100,   //TODO which number is selected, from localstorage;
      slippageTolerance: [0.1, 0.5, 1, 'slippage:N'],    //TODO getLocalstorage Customer slippage
      priceImpact: `${(Math.abs(priceImpact) * 100).toFixed(2)}%`,
      minimumReceived: minimumReceived.toFixed(7),
      fee: `${(Math.abs(fee + takerRate) / 100).toFixed(2)}%`,
  }

  const swapProps: SwapProps<IBData<any>, string, any> = {
      swapTradeData: {
          sell: {
              belong: base,
              balance: availToShow,
              tradeValue: baseAmt as any,
          }, buy: {
              belong: quote,
              balance: quoteToShow,
              tradeValue: quoteAmt,
          },
          slippage: slippageTolerance,
          __cache__: {}
      },
      tradeCalcData,

      onSwapClick: (tradeData: SwapTradeData<any>) => {
          onSwap()
      },

      handleSwapPanelEvent: async (swapData, switchType) => {
          return new Promise((res) => {

              console.log('swapData:', swapData)

              const { type, tradeData, to, } = swapData
              let right = undefined
              switch (switchType) {
                  case SwapType.SEll_CLICK:
                      break
                  case SwapType.BUY_CLICK:
                      break
                  case SwapType.SELL_SELECTED:
                      const quoteAmtVal = onBaseAmtInputChange(tradeData?.sell);
                      if (quoteAmtVal !== undefined) {
                          tradeData.buy = { ...tradeData.buy, tradeValue: quoteAmtVal }
                      }
                      setBase(tradeData.sell.belong)
                      right = getRight(ammpools?.pairs, tradeData.sell.belong, tokens?.tokenSymbolMap)

                      console.log('right:', right)

                      if (right) {
                          setRight(right.coinMapRight)
                          setQuote(right.right[0])
                      } else {
                          setQuote(undefined)
                      }
                      break
                  case SwapType.BUY_SELECTED:
                      console.log()
                      onQuoteAmtInputChange(tradeData?.buy)
                      setQuote(tradeData.buy.belong)
                      break
                  case SwapType.EXCHANGE_CLICK:
                      setBaseAmt(0)
                      setQuoteAmt(0)

                      tradeData.buy = { ...tradeData.buy, balance: availToShow, tradeValue: 0 }
                      tradeData.sell = { ...tradeData.sell, balance: quoteToShow, tradeValue: 0 }

                      // console.log('balances:', balances.symbol)
                      console.log('sell:', tradeData.sell)
                      console.log('buy:', tradeData.buy)

                      console.log('availToShow:', availToShow)
                      console.log('quoteToShow:', quoteToShow)

                      setBase(tradeData.sell?.belong)
                      right = getRight(ammpools?.pairs, tradeData.sell.belong, tokens?.tokenSymbolMap)
                      if (right) {
                          console.log('right:', right.right[0])
                          setRight(right.coinMapRight)
                          setQuote(right.right[0])
                      } else {
                          setQuote(tradeData.buy?.belong)
                      }
                      break
                  default:
                      break
              }

              res()
          })
      },
  }

  setSwapProps(swapProps)

}, [slippageTolerance,
  priceImpact,
  fee,
  takerRate,
  left,
  right,
  walletMap,
  minimumReceived, availToShow, quoteToShow, baseAmt, quoteAmt, currentPrice,
  onBaseAmtInputChange,
  onQuoteAmtInputChange,
  onSwap])

  // return-----
  return {
    base,
    quote,
    tokens,
    markets,
    market,
    poolAddress,
    swapProps,
  }

}
