import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import {  WalletStates } from './interface';
import { STATUS } from '../constant';


const initialState:Required<WalletStates>  = {
    wallet:{},
    status:'UNSET',
    errorMessage:null,
}
const walletSlice:Slice = createSlice({
    name: 'walletS',
    initialState,
    reducers: {
        getWallet(state, action:PayloadAction<string | undefined>) {
            state.status = STATUS.PENDING
        },
        getWalletStatus(state, action: PayloadAction<WalletStates>) {
            // @ts-ignore
            if (action.error) {
                state.status =  STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            state.wallet = {...action.payload.wallet};
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }
    },
});
export { walletSlice };
export const { getWallet, getWalletStatus,statusUnset } = walletSlice.actions
