import { useState } from 'react'

import TradePanelUi from './TradePanel'
import { useGetMyTrades, useGetRecentTrades, } from './hooks'

const TradePanel = (props: any) => {

  const { market, poolAddress, base, quote } = props

  console.log('TradePanel market:', market)

  const { myTrades } = useGetMyTrades(market)

  const { recentTrades } = useGetRecentTrades(poolAddress)

  const [isAllTrade, setIsAllTrade] = useState(false)

  return (
    <TradePanelUi trades={isAllTrade? recentTrades: myTrades} isAllTrade={isAllTrade} 
    setIsAllTrade={setIsAllTrade} />
  )

}

export default TradePanel
