import { all, call, put, takeLatest } from "redux-saga/effects";
import { getWallet, getWalletStatus } from './reducer';
import { CoinKey, ErrorObject, PairKey, WalletCoin } from '@loopring-web/component-lib/src/static-resource';
type WalletMap<R extends object> = {
    [key in CoinKey<R>|PairKey<R>]?:WalletCoin<R>
}

const getWalletBalance = async <R extends object>() => new Promise((resolve,reject) => {
    //TODO: check is connect and active and assign wallet
    //TODO: if not reject directory, any error happen will clean the
    // await sdk
    const data:WalletMap<R> = {
        // @ts-ignore
        ETH: {
            belong: 'ETH',
            count: 11,
        },
        LRC: {
            belong: 'LRC',
            count: 11111111111111,
        },
        'AMM-ETH-LRC':{
            belong: 'AMM-ETH-LRC',
            count: 11,
        }
    }
    resolve({data}) ;
    if(false){
        const errorMessage:ErrorObject={
            from:'get wallet balace map',
            timestamp:Date.now(),
            messageKey:"translatedkey"
        }
        reject(errorMessage)
    }
});

export function* getPostsSaga() {
    try {
        //
        const {data} = yield call(getWalletBalance);
        yield put(getWalletStatus({wallet:data}));
    } catch (err) {
        yield put(getWalletStatus(err));
    }
}

export default function* walletSaga() {
    yield all([takeLatest(getWallet, getPostsSaga)]);
}
