import { Box, Grid, } from '@material-ui/core'
import { WithTranslation, withTranslation } from 'react-i18next'
import SwapPanel from './panel/SwapPanel'
import { useSwapNew, } from './hook'
import BasicInfoPanel from './panel/BasicInfoPanel'
import TradePanel from './panel/TradePanel'
import styled from 'styled-components'

const FixedStyle = styled(Box)`
  @media only screen and (min-height: 680px ) and (min-width:1024px){
    position: fixed;
  }
`

const SwapPage = withTranslation('common')(({ ...rest }: WithTranslation) => {

    const { base, quote, tokens, market, poolAddress, swapProps } = useSwapNew()

    const props = { base, quote, tokens, market }

    return <>
        <Grid container marginRight={3} alignContent={'flex-start'} >
            <BasicInfoPanel {...{ ...rest, swapProps, props }} />
            <TradePanel market={market} poolAddress={poolAddress} />
        </Grid>

        <Box display={'flex'} style={{ minWidth: 'var(--swap-box-width)' }} >
            <FixedStyle>
                <SwapPanel {...{
                    ...rest, swapProps
                }} />
            </FixedStyle>

        </Box>

    </>
});


export default SwapPage
